<template>
    <div class="submission">
        <!-- 占位 -->
        <div style="height:10px;"></div>
        <div class="upload-container" id="drop_area"  >   
            <div class="dropUp"  >
                <el-upload
                    class="upload-demo"
                    :accept="'.json'"
                    :on-change="handleSuccess"
                    :auto-upload="false"
                    :file-list="fileList"
                    multiple
                    drag
                    ref="el"
                    :limit="1">
                    <!-- <el-upload
                    class="upload-demo"
                    action="http://120.26.95.9:8081/upload/"
                    :accept="'.json'"
                    :on-change="handleSuccess"
                    :auto-upload="false"
                    :file-list="fileList"
                    multiple
                    drag
                    ref="el"
                    :limit="1"> -->
                <img src="../../assets/images/云.svg" >
                <div style="color:white;font-size: large;font-weight: bold;">上传文件</div><br/>
                <div class="upload__text">点击图标或将文件文件拖拽到此处上传</div>
                <div class="filestyle">支持文件格式：json等</div>
            </el-upload>
            </div>        
        </div>
        <!-- 占位 -->
        <div style="height:25px;"></div>
        <el-select v-model="selectedValue1" placeholder="请选择输入的规范" style="margin-right: 5%;">
            <el-option v-for="(selecrtSBOM1,index) in SBOM" :label='selecrtSBOM1' :value=selecrtSBOM1></el-option>
            
        </el-select>
        
        <el-select v-model="selectedValue2" placeholder="请选择目标SBOM规范" style="margin-left: 5%;">
            <el-option v-for="(selecrtSBOM2,index) in SBOM" :label='selecrtSBOM2' :value=selecrtSBOM2></el-option>
        </el-select>
        <div class="report" style="height: 10%; margin-top: 1.5%;">
                <el-button  class="btn-sub"  size="medium" @click="postFiles">开始转换</el-button>
        </div>

        <div class="below" style="height: 30%; margin-top: 1.5%;">
            <div class="table">
                <table>
                    <caption>最近提交</caption>
                    <thead>
                        <tr>
                            <th style="border: 1px solid #7400bf;">应用名称</th>
                            <th style="border: 1px solid #8946b8;">原SBOM规范</th>
                            <th style="border: 1px solid #ac7eb0;">现SBOM规范</th>
                            <th style="border: 1px solid  #df8cab;">上传时间</th>
                            <th style="border: 1px solid #ff7bab;">操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr>
                            <td style="border: 1px solid #7400bf;"><input type="radio" name="Appname" value="DEMO" v-model="selectDemo"><span>{{name}}</span></td>
                            <td style="border: 1px solid #8946b8;">{{origin}}</td>
                            <td style="border: 1px solid #ac7eb0;">{{now}}</td>
                            <td style="border: 1px solid  #df8cab;">{{time}}</td>
                            <td style="border: 1px solid #ff7bab;">
                            
                                <button class="btn-sc" style="border:none;background:none;cursor:pointer;" @click="downloadSBOM1">
                                    <span style="color: #ff7bab;">原始文件</span>
                                </button>
                                <button class="btn-sc" style="border:none;background:none;cursor:pointer;" @click="downloadSBOM2">
                                    <span style="color: #ff7bab;">转换文件</span>
                                </button>
                                <!-- <button class="btn-sc" style="border:none;background:none;"><span style="color: red;">删除</span></button> -->
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>


<script>
export default {
    // ...
    data(){
        return {
            selectDemo: false,
            fileList:[],
            handleSuccessflag:true,
            SBOM:['GEEKSBOM','CycloneDX','SPDX'],
            selectedValue2:'',
            selectedValue1:'',
            origin:'',
            now:'',
            name:'',
            time:'',
        }
    },

    methods: {
        downloadSBOM1(){
            if(!this.origin){
                alert('请上传文件');
                return;
            }
                
            let a = document.createElement("a");
            a.href = "/static/"+this.origin+".json";
            a.style.display = 'none'           // 隐藏a标签
            a.setAttribute('download', '')
            document.body.appendChild(a)       // 将a标签追加到文档对象中
            a.click()                          // 模拟点击了a标签，会触发a标签的href的读取，浏览器就会自动下载了
            a.remove()                        // 一次性的，用完就删除a标签

        },
        downloadSBOM2() {
            if(!this.now){
                alert('请上传文件');
                return;
            }
            let a = document.createElement("a");
            a.href = "/static/"+this.now+".json";
            a.style.display = 'none'           // 隐藏a标签
            a.setAttribute('download', '')
            document.body.appendChild(a)       // 将a标签追加到文档对象中
            a.click()                          // 模拟点击了a标签，会触发a标签的href的读取，浏览器就会自动下载了
            a.remove()                        // 一次性的，用完就删除a标签
        },
        handleAlert(){
            alert('您还不是会员');
            return false;
        },
        goToList(){
            if(this.selectDemo){
                // this.$router.push('/submission/reportList');
                console.log(_this.selectedValue2);
                console.log(_this.selectedValue1);
            }
            else{
                alert('您还没有选中的项目！')
            }
        },

        //start
        handleSuccess(response,file) {

            if(!this.handleSuccessflag) return;

            console.log(response);
            const isAPK = response.raw.type === 'application/json';
            if(isAPK){
                console.log(response.raw.name + "上传成功");
                this.fileList.push(file);
                this.$message({
                        message: '上传成功',
                        type: 'success'
                    });

                    setTimeout(() => {
                    this.$message.closeAll();
                }, 2000);
            }
            else{
                this.fileList=[];
                this.$message.error('仅限上传json等格式文件');
            }
        },

        postFiles() {
            var _this=this;
            if(this.fileList.length === 0){
                alert('请上传文件');
                return;
            }
            this.handleSuccessflag=false;
            this.$refs.el.submit();

            
            this.$message.info('即将生成...');
            setTimeout(()=>{
                // this.$router.push('/console/detail')
                var timestamp = Date.parse(new Date());
                timestamp = timestamp / 1000;
                var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
                var Y = date.getFullYear() + '.';
                var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1):date.getMonth()+1) + '.';
                var D = (date.getDate()< 10 ? '0'+date.getDate():date.getDate())+ ' ';
                var h = (date.getHours() < 10 ? '0'+date.getHours():date.getHours())+ ':';
                var m = (date.getMinutes() < 10 ? '0'+date.getMinutes():date.getMinutes()) ;
    
                _this.time=Y+M+D+h+m;
                _this.name="dubbo";
                _this.origin=_this.selectedValue1;
                _this.now=_this.selectedValue2;
                console.log(_this.time);
                console.log(_this.name);
            },300);
            
        },

        //end
    
    },
}
</script>

